"use client"; // Add this at the top for client-side functionality

import { draftMode } from "next/headers";
import { VisualEditing } from "next-sanity";
import { DisableDraftMode } from "@/components/DisableDraftMode";
import { SanityLive } from "@/sanity/lib/live";
import Header from "../header";
import Footer from "../footer";
import Head from 'next/head'
import { useEffect, useState } from 'react';

export default function FrontendLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const [isScrollingUp, setIsScrollingUp] = useState(false);
  let lastScroll = 0;

  useEffect(() => {
    const handleScroll = () => {
      const currentScroll = window.pageYOffset;

      if (currentScroll <= 0) {
        setIsScrollingUp(false);
        return;
      }

      setIsScrollingUp(currentScroll < lastScroll);
      lastScroll = currentScroll;
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <>
      <Head>
        <link
          rel="stylesheet"
          href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.5.0/css/all.min.css"
          integrity="sha512-SOME_HASH_HERE"
          crossOrigin="anonymous"
          referrerPolicy="no-referrer"
        />
      </Head>

      <body className={`home wp-singular page-template page-template-page-no-title-no-sidebar page page-id-39923 wp-custom-logo wp-embed-responsive wp-theme-career-fse no-sidebar ${isScrollingUp ? 'sticker-header' : ''}`}>
        <div className="wp-site-blocks">
          <Header />
          <main className="wp-block-group site-main has-global-padding is-layout-constrained wp-block-group-is-layout-constrained">
            <div className="wp-block-group container is-layout-flow wp-block-group-is-layout-flow" style={{ marginTop: 0 }}>
              <div className="entry-content wp-block-post-content has-global-padding is-layout-constrained wp-block-post-content-is-layout-constrained">
                <div className="wp-block-multidots-multipurpose-block is-block-center mdcareer-homepage-main">
                  {children}
                  <SanityLive />
                  {draftMode().isEnabled && (
                    <>
                      <DisableDraftMode />
                      <VisualEditing />
                    </>
                  )}
                </div>
              </div>
            </div>
          </main>
        </div>
        <Footer />
      </body>
    </>
  );
}